//main styles
html {
    font-size: 10px;
}

.main-wrapper {
    padding: 0 0 0 0;
    min-width: $min-width;
    width: 100%;
    position: relative;
    overflow: hidden;
    min-height: 100%;
    display: -webkit-box;
    display: -moz-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    flex-direction: column;
    -webkit-flex-direction: column;
    -ms-flex-direction: column;
    justify-content: flex-start;
    -webkit-justify-content: flex-start;
    -ms-flex-pack: start;
}

.wrapper {
    min-width: $min-width;
    max-width: none;
    padding: 0 1.5rem 0 1.5rem;
    margin-left: 18.7%;
    position: relative;
    margin-right: 7.5%;
}

/* titles */

p {

    padding: 1.5rem 0 1.5rem 0;
    font-weight: 400;
}

h1 {

    padding: 1.5rem 0 1.5rem 0;
    font-weight: 400;
}

h2 {

    padding: 1.5rem 0 1.5rem 0;
    font-weight: 400;
}

h3 {

    padding: 1.5rem 0 1.5rem 0;
    font-weight: 400;
}

/* text position */

.text-left {
    text-align: left !important
}

.text-center {
    text-align: center !important
}

.text-right {
    text-align: right !important
}

.nowrap {
    white-space: nowrap !important;
}


/* loader */

.loaded .main-wrapper {
    visibility: hidden;
}

.icon-load {
    background: url(../img/loader.gif) no-repeat left top;
    width: 40px;
    height: 40px;
    position: fixed;
    left: 50%;
    top: 50%;
    margin-left: -20px;
    margin-left: -20px;
    display: none;
}

.loaded .icon-load {
    display: block;
}


